/* @group Tabs */
#content-views {
  float: left;
  margin-top: 5px;
  background-color: transparent;
}
#content-views li {
  display: inline;
  list-style: none;
}
/* fix shift on content views list under ie6 */
/* .ie6 is set on <html> by Modernizr library */
.ie6 ul#content-views {
  margin: -2px -3px 0px -3px;
}
#content-views a {
  color: White;
}
/* Shared CSS between the two tab types */
#content-views a {
  /* this isn't supported by all browsers, so we keep it in a separate rule */
  display: inline-block;
}
#content-views a {
  line-height: 2em;
  padding: 0 0.75em;
  text-decoration: none;
  border: none;
  margin: 0 ;
}
#content-views li.selected a {
  font-weight: bold;
}
#content-views li.selected a,
#content-views li a:hover {
  background: White;
  color: Black;
  border-radius: 4px;
}
#content li.formTab a.notify,
#content-core li.formTab a.notify {
  background-color: red;
  color: White;
}
div.field span.required:after {
  content: "■";
  color: Red;
  padding-left: 0.2em;
}
#content fieldset.formPanel,
#content-core fieldset.formPanel {
  border: none;
}
#content fieldset.formPanel.hidden,
#content-core fieldset.formPanel.hidden {
  display: none;
}
#content div.formControls input.hidden,
#content-core div.formControls input.hidden {
  display: none;
}
/* @end */
/* @group Pulldown menus */
/* @group Personal tools pulldown */
#portal-personaltools dt {
  font-weight: normal;
}
#portal-personaltools dt.actionMenuHeader a:after {
  content: " ▼";
}
#portal-personaltools dd {
  right: 0;
  padding-top: 0.5em;
}
#portal-personaltools dd {
  background: #e3e3e3;
  padding-bottom: 0.5em;
}
#portal-personaltools dd a {
  line-height: 2em;
  padding: 0 0.75em;
}
#portal-personaltools dd a:hover {
  background: #205c90;
  color: White;
}
/* @end */
/* @group Content pulldowns */
#contentActionMenus li {
  display: inline;
}
#contentActionMenus dl.actionMenu a {
  background-color: transparent;
  color: White;
  z-index: 5;
}
#contentActionMenus dl.actionMenu.activated dd {
  background-color: #999999;
  color: White;
  z-index: 5;
}
#contentActionMenus dl.actionMenu.activated dd {
  border-bottom: 0.5em solid #999999;
  right: -3px;
}
#contentActionMenus dl.actionMenu.activated dd a:hover,
#contentActionMenus dl.actionMenu.activated dd .actionMenuSelected {
  background-color: White !important;
  color: #666;
}
#contentActionMenus dl.actionMenu {
  line-height: 20px;
}
#folderHeader,
#contextHeader {
  border-bottom: 2px solid white;
}
#contextHeader {
  margin-top: 5px;
}
/* @end */
#contentActionMenus {
  float: right;
  background: transparent;
  margin: 0;
  position: relative;
  top: 10px;
  right: -0.24em;
  padding: 4px 4px -4px 0;
}
.actionMenuContent {
  padding: 0 0.25em;
}
dl.actionMenu {
  float: right;
  white-space: nowrap;
  position: relative;
  margin-left: 0.5em;
  margin-bottom: 0px;
}
dl.actionMenu a,
dl.actionMenu div {
  display: block !important;
  white-space: nowrap;
}
dl.actionMenu dt {
  font-weight: normal;
}
span.arrowDownAlternative {
  font-size: 80%;
}
dl.actionMenu.deactivated dt {
  margin: 0 0.25em;
}
dl.actionMenu.deactivated dd {
  display: none;
}
dl.actionMenu.activated dd {
  position: absolute;
  width: auto;
  min-width: 100%;
  border-bottom-right-radius: 0.5em;
  border-bottom-left-radius: 0.5em;
  -webkit-border-bottom-right-radius: 0.5em;
  -webkit-border-bottom-left-radius: 0.5em;
  -moz-border-radius-bottomleft: 0.5em;
  -moz-border-radius-bottomright: 0.5em;
}
dl.actionMenu.activated dt a,
dl.actionMenu.activated dd a,
dl.actionMenu.activated dd div {
  margin: 0 0.25em;
}
/* @end */
/* @group Content History */
div.historyRecord {
  margin-bottom: 1em;
}
div.historyTools ul {
  float: right;
  margin: 0.5em 1em;
}
div.historyTools ul li {
  display: inline;
  padding: 0 0.015em;
}
div.historyTools ul li a {
  border-bottom: none;
}
div.historyTools form {
  display: inline;
}
.historyByLine {
  background: #ddd;
  padding: 0.5em;
  border-top-right-radius: 0.5em;
  border-top-left-radius: 0.5em;
  -webkit-border-top-right-radius: 0.5em;
  -webkit-border-top-left-radius: 0.5em;
  -moz-border-radius-topleft: 0.5em;
  -moz-border-radius-topright: 0.5em;
}
div.historyLinks {
  float: right;
  clear: right;
  margin: 0.5em 1em 0 0;
}
div.historyComment {
  padding: 0.5em 1em;
  border: 1px solid #ddd;
}
div.historyComment p {
  margin: 0;
  padding-top: 1px;
}
a.historyComparePrevious {
  border-top: 1px dotted #ccc;
  display: block;
  margin-top: 3em;
  text-align: center;
}
a.historyComparePrevious span {
  background-color: white;
  position: relative;
  top: -0.75em;
  padding: 0 0.5em;
}
/* @end */
ul.formTabs {
  background: transparent;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  border-bottom: 1px solid #dddddd;
  margin: 0 0 10px;
  list-style-type: none;
}
ul.formTabs > li {
  position: relative;
  display: block;
}
ul.formTabs > li > a {
  position: relative;
  display: block;
  padding: 10px 15px;
}
ul.formTabs > li > a:hover,
ul.formTabs > li > a:focus {
  text-decoration: none;
  background-color: #eeeeee;
}
ul.formTabs > li.disabled > a {
  color: #999999;
}
ul.formTabs > li.disabled > a:hover,
ul.formTabs > li.disabled > a:focus {
